// eslint-disable

import React from 'react';
import PT from 'prop-types';
import { Link, graphql } from 'gatsby';

import Bio from '../components/bio';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { rhythm } from '../utils/typography';
import { CardBox } from '../components/cardbox';

const description = `
This is how it looks when a programmer starts to write something else than code. The
personal blog of one programmer.
`;

class BlogIndex extends React.PureComponent {
  render() {
    const { data, location } = this.props;
    const siteTitle = data.site.siteMetadata.title;
    const posts = data.allMarkdownRemark.edges;

    return (
      <Layout location={location} title={siteTitle} withVerticalPadding={false} withFooter>
        <SEO
          title="Martin Schnürer | Blog"
          description={description}
          keywords={['blog', 'javascript', 'react', 'personal', 'calculators']}
        />
        <CardBox>
          <Bio showWrittenBy={false} />
          {posts.map(({ node }) => {
            if (node.frontmatter.publish !== undefined && node.frontmatter.publish === false) {
              return null;
            }
            const title = node.frontmatter.title || node.fields.slug;
            return (
              <div key={node.fields.slug}>
                <h3 style={{ marginBottom: rhythm(1 / 4) }}>
                  <Link style={{ boxShadow: 'none' }} to={node.fields.slug}>
                    {title}
                  </Link>
                </h3>
                <small>{node.frontmatter.date}</small>
                <p dangerouslySetInnerHTML={{ __html: node.excerpt }} />
              </div>
            );
          })}
        </CardBox>
      </Layout>
    );
  }
}

BlogIndex.propTypes = {
  data: PT.objectOf(PT.any).isRequired,
  location: PT.objectOf(PT.any).isRequired,
};


export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          timeToRead
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            publish
          }
        }
      }
    }
  }
`;
